html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
}

h1, h2, h3 {
  margin-top: 0;
}

a, a:visited, a:active {
  text-decoration: none;
}

:root {
  --Limegreen: hsl(163, 72%, 41%);
  --Brightred: hsl(356, 69%, 56%);
  --Facebook: hsl(208, 92%, 53%);
  --Twitter: hsl(203, 89%, 53%);
  --Instagram-from:hsl(36, 97%, 70%);
  --Instagram-to:hsl(328, 70%, 58%) ;
  --YouTube: hsl(348, 97%, 39%);
  --toggle-bg-from:hsl(210, 78%, 56%);
  --toggle-bg-to:hsl(146, 68%, 55%);
  --Toggle-btn:hsl(230, 22%, 74%);
  --dark-bg:hsl(230, 17%, 14%);
  --dark-top-bg: hsl(232, 19%, 15%);
  --dark-card-bg: hsl(228, 28%, 20%);
  --dark-card-text:hsl(228, 34%, 66%);
  --dark-text:hsl(0, 0%, 100%);
  --light-bg: hsl(0, 0%, 100%);
  --light-top-bg: hsl(225, 100%, 98%);
  --light-card-bg: hsl(227, 47%, 96%);
  --light-card-text: hsl(228, 12%, 44%);
  --light-text: hsl(230, 17%, 14%);
}

:root {
  --font-inter:"Inter" ,sans-serif;
}

.card {
  padding: 1.25rem;
}

.toggle_button {
  width: rem(16);
  height: rem(16);
  border-radius: 50%;
  background-color: var(--Toggle-btn);
}